"use client";

import { useState } from "react";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import {
    Card,
    CardContent,
    CardDescription,
    CardHeader,
    CardTitle,
} from "@/components/ui/card";
import { toast } from "sonner";
import { Lock, Shield, Key } from "lucide-react";

export function PasswordForm() {
    const [isLoading, setIsLoading] = useState(false);
    const [currentPassword, setCurrentPassword] = useState("");
    const [newPassword, setNewPassword] = useState("");
    const [confirmPassword, setConfirmPassword] = useState("");

    const handleSubmit = async (e: React.FormEvent) => {
        e.preventDefault();
        setIsLoading(true);

        if (newPassword !== confirmPassword) {
            toast.error("New passwords do not match");
            setIsLoading(false);
            return;
        }

        try {
            const response = await fetch("/api/profile/password", {
                method: "PUT",
                headers: { "Content-Type": "application/json" },
                body: JSON.stringify({
                    currentPassword,
                    newPassword,
                }),
            });

            const data = await response.json();

            if (!response.ok) {
                throw new Error(data.error || "Failed to update password");
            }

            toast.success("Password updated successfully");
            setCurrentPassword("");
            setNewPassword("");
            setConfirmPassword("");
        } catch (error) {
            if (error instanceof Error) {
                toast.error(error.message);
            } else {
                toast.error("Failed to update password");
            }
        } finally {
            setIsLoading(false);
        }
    };

    return (
        <form onSubmit={handleSubmit} className="space-y-6 mt-6">
            <Card className="profile-card border-0 bg-gradient-to-br from-red-50/50 via-white to-rose-50/50 dark:from-red-950/20 dark:via-background dark:to-rose-950/20 shadow-lg shadow-red-500/5 dark:shadow-red-500/10 backdrop-blur-sm transition-all duration-300 hover:shadow-xl hover:shadow-red-500/10 dark:hover:shadow-red-500/20">
                <CardHeader className="space-y-1 pb-4">
                    <div className="flex items-center gap-2">
                        <div className="p-2 rounded-lg bg-gradient-to-br from-red-500 to-rose-600 text-white">
                            <Shield className="h-5 w-5" />
                        </div>
                        <CardTitle className="text-2xl bg-gradient-to-r from-red-600 to-rose-600 dark:from-red-400 dark:to-rose-400 bg-clip-text text-transparent">
                            Security Settings
                        </CardTitle>
                    </div>
                    <CardDescription className="text-sm">
                        Update your password to keep your account secure
                    </CardDescription>
                </CardHeader>
                <CardContent className="space-y-5">
                    <div className="space-y-2 group">
                        <Label htmlFor="currentPassword" className="text-sm font-medium flex items-center gap-2">
                            <Lock className="h-3.5 w-3.5 text-red-500" />
                            Current Password
                        </Label>
                        <Input
                            id="currentPassword"
                            type="password"
                            value={currentPassword}
                            onChange={(e) => setCurrentPassword(e.target.value)}
                            placeholder="Enter your current password"
                            required
                            className="transition-all duration-200 focus:scale-[1.01] focus:shadow-md"
                        />
                    </div>

                    <div className="space-y-2 group">
                        <Label htmlFor="newPassword" className="text-sm font-medium flex items-center gap-2">
                            <Key className="h-3.5 w-3.5 text-rose-500" />
                            New Password
                        </Label>
                        <Input
                            id="newPassword"
                            type="password"
                            value={newPassword}
                            onChange={(e) => setNewPassword(e.target.value)}
                            placeholder="Enter your new password (min. 6 characters)"
                            required
                            minLength={6}
                            className="transition-all duration-200 focus:scale-[1.01] focus:shadow-md"
                        />
                        <p className="text-xs text-muted-foreground">
                            Password must be at least 6 characters long
                        </p>
                    </div>

                    <div className="space-y-2 group">
                        <Label htmlFor="confirmPassword" className="text-sm font-medium flex items-center gap-2">
                            <Key className="h-3.5 w-3.5 text-pink-500" />
                            Confirm New Password
                        </Label>
                        <Input
                            id="confirmPassword"
                            type="password"
                            value={confirmPassword}
                            onChange={(e) => setConfirmPassword(e.target.value)}
                            placeholder="Confirm your new password"
                            required
                            minLength={6}
                            className="transition-all duration-200 focus:scale-[1.01] focus:shadow-md"
                        />
                    </div>
                </CardContent>
                <div className="p-6 pt-0">
                    <Button
                        type="submit"
                        className="w-full h-11 text-base font-semibold bg-gradient-to-r from-red-600 via-rose-600 to-pink-600 hover:from-red-700 hover:via-rose-700 hover:to-pink-700 text-white shadow-md hover:shadow-lg transition-all duration-200 hover:scale-[1.02] disabled:opacity-50 disabled:cursor-not-allowed disabled:hover:scale-100"
                        disabled={isLoading}
                    >
                        {isLoading ? (
                            <>
                                <div className="mr-2 h-4 w-4 animate-spin rounded-full border-2 border-white border-t-transparent" />
                                Updating Password...
                            </>
                        ) : (
                            <>
                                <Shield className="mr-2 h-4 w-4" />
                                Update Password
                            </>
                        )}
                    </Button>
                </div>
            </Card>

            <style jsx>{`
                .profile-card {
                    position: relative;
                    overflow: hidden;
                }
                
                .profile-card::before {
                    content: '';
                    position: absolute;
                    top: 0;
                    left: 0;
                    right: 0;
                    height: 2px;
                    background: linear-gradient(90deg, transparent, currentColor, transparent);
                    opacity: 0;
                    transition: opacity 0.3s;
                }
                
                .profile-card:hover::before {
                    opacity: 0.5;
                }
            `}</style>
        </form>
    );
}
