"use client";

import { useState } from "react";
import { useRouter } from "next/navigation";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { Textarea } from "@/components/ui/textarea";
import {
  Card,
  CardContent,
  CardDescription,
  CardHeader,
  CardTitle,
} from "@/components/ui/card";
import { Badge } from "@/components/ui/badge";
import { toast } from "sonner";
import {
  User,
  Briefcase,
  Target,
  Award,
  Building2,
  Plus,
  X,
  Save,
  Sparkles
} from "lucide-react";

interface ProfileFormProps {
  user: {
    id: string;
    email: string;
    name: string | null;
    avatar: string | null;
  };
  profile: {
    bio: string | null;
    yearsExperience: number | null;
    skills: string[];
    targetCompanies: string[];
    targetRole: string | null;
  } | null;
}

export function ProfileForm({ user, profile }: ProfileFormProps) {
  const router = useRouter();
  const [isLoading, setIsLoading] = useState(false);
  const [name, setName] = useState(user.name || "");
  const [bio, setBio] = useState(profile?.bio || "");
  const [yearsExperience, setYearsExperience] = useState(
    profile?.yearsExperience?.toString() || ""
  );
  const [targetRole, setTargetRole] = useState(profile?.targetRole || "");
  const [skills, setSkills] = useState<string[]>(profile?.skills || []);
  const [targetCompanies, setTargetCompanies] = useState<string[]>(
    profile?.targetCompanies || []
  );
  const [newSkill, setNewSkill] = useState("");
  const [newCompany, setNewCompany] = useState("");

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setIsLoading(true);

    try {
      const response = await fetch("/api/profile", {
        method: "PUT",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({
          name,
          bio,
          yearsExperience: yearsExperience ? parseInt(yearsExperience) : null,
          targetRole,
          skills,
          targetCompanies,
        }),
      });

      if (!response.ok) {
        throw new Error("Failed to update profile");
      }

      toast.success("Profile updated successfully");
      router.refresh();
    } catch {
      toast.error("Failed to update profile");
    } finally {
      setIsLoading(false);
    }
  };

  const addSkill = () => {
    if (newSkill.trim() && !skills.includes(newSkill.trim())) {
      setSkills([...skills, newSkill.trim()]);
      setNewSkill("");
    }
  };

  const removeSkill = (skill: string) => {
    setSkills(skills.filter((s) => s !== skill));
  };

  const addCompany = () => {
    if (newCompany.trim() && !targetCompanies.includes(newCompany.trim())) {
      setTargetCompanies([...targetCompanies, newCompany.trim()]);
      setNewCompany("");
    }
  };

  const removeCompany = (company: string) => {
    setTargetCompanies(targetCompanies.filter((c) => c !== company));
  };

  return (
    <form onSubmit={handleSubmit} className="space-y-6 profile-form-wrapper">
      {/* Basic Information Card */}
      <Card className="profile-card border-0 bg-gradient-to-br from-blue-50/50 via-white to-purple-50/50 dark:from-blue-950/20 dark:via-background dark:to-purple-950/20 shadow-lg shadow-blue-500/5 dark:shadow-blue-500/10 backdrop-blur-sm transition-all duration-300 hover:shadow-xl hover:shadow-blue-500/10 dark:hover:shadow-blue-500/20">
        <CardHeader className="space-y-1 pb-4">
          <div className="flex items-center gap-2">
            <div className="p-2 rounded-lg bg-gradient-to-br from-blue-500 to-purple-600 text-white">
              <User className="h-5 w-5" />
            </div>
            <CardTitle className="text-2xl bg-gradient-to-r from-blue-600 to-purple-600 dark:from-blue-400 dark:to-purple-400 bg-clip-text text-transparent">
              Basic Information
            </CardTitle>
          </div>
          <CardDescription className="text-sm">Your personal details and professional identity</CardDescription>
        </CardHeader>
        <CardContent className="space-y-5">
          <div className="space-y-2 group">
            <Label htmlFor="email" className="text-sm font-medium flex items-center gap-2">
              <Sparkles className="h-3.5 w-3.5 text-blue-500" />
              Email Address
            </Label>
            <Input
              id="email"
              value={user.email}
              disabled
              className="bg-muted/50 border-muted transition-all duration-200"
            />
          </div>

          <div className="space-y-2 group">
            <Label htmlFor="name" className="text-sm font-medium flex items-center gap-2">
              <User className="h-3.5 w-3.5 text-purple-500" />
              Full Name
            </Label>
            <Input
              id="name"
              value={name}
              onChange={(e) => setName(e.target.value)}
              placeholder="Enter your name"
              className="transition-all duration-200 focus:scale-[1.01] focus:shadow-md"
            />
          </div>

          <div className="space-y-2 group">
            <Label htmlFor="bio" className="text-sm font-medium flex items-center gap-2">
              <Briefcase className="h-3.5 w-3.5 text-indigo-500" />
              Professional Bio
            </Label>
            <Textarea
              id="bio"
              value={bio}
              onChange={(e) => setBio(e.target.value)}
              placeholder="Tell us about yourself, your expertise, and your career goals..."
              rows={4}
              className="transition-all duration-200 focus:scale-[1.01] focus:shadow-md resize-none"
            />
          </div>

          <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
            <div className="space-y-2 group">
              <Label htmlFor="experience" className="text-sm font-medium flex items-center gap-2">
                <Award className="h-3.5 w-3.5 text-amber-500" />
                Years of Experience
              </Label>
              <Input
                id="experience"
                type="number"
                min="0"
                max="50"
                value={yearsExperience}
                onChange={(e) => setYearsExperience(e.target.value)}
                placeholder="e.g., 5"
                className="transition-all duration-200 focus:scale-[1.01] focus:shadow-md"
              />
            </div>
            <div className="space-y-2 group">
              <Label htmlFor="targetRole" className="text-sm font-medium flex items-center gap-2">
                <Target className="h-3.5 w-3.5 text-green-500" />
                Target Role
              </Label>
              <Input
                id="targetRole"
                value={targetRole}
                onChange={(e) => setTargetRole(e.target.value)}
                placeholder="e.g., Senior Software Engineer"
                className="transition-all duration-200 focus:scale-[1.01] focus:shadow-md"
              />
            </div>
          </div>
        </CardContent>
      </Card>

      {/* Skills Card */}
      <Card className="profile-card border-0 bg-gradient-to-br from-emerald-50/50 via-white to-teal-50/50 dark:from-emerald-950/20 dark:via-background dark:to-teal-950/20 shadow-lg shadow-emerald-500/5 dark:shadow-emerald-500/10 backdrop-blur-sm transition-all duration-300 hover:shadow-xl hover:shadow-emerald-500/10 dark:hover:shadow-emerald-500/20">
        <CardHeader className="space-y-1 pb-4">
          <div className="flex items-center gap-2">
            <div className="p-2 rounded-lg bg-gradient-to-br from-emerald-500 to-teal-600 text-white">
              <Award className="h-5 w-5" />
            </div>
            <CardTitle className="text-2xl bg-gradient-to-r from-emerald-600 to-teal-600 dark:from-emerald-400 dark:to-teal-400 bg-clip-text text-transparent">
              Technical Skills
            </CardTitle>
          </div>
          <CardDescription className="text-sm">
            Showcase your technical expertise and capabilities
          </CardDescription>
        </CardHeader>
        <CardContent className="space-y-4">
          <div className="flex gap-2">
            <div className="relative flex-1">
              <Input
                value={newSkill}
                onChange={(e) => setNewSkill(e.target.value)}
                placeholder="Add a skill (e.g., System Design, React, Python)"
                onKeyDown={(e) => e.key === "Enter" && (e.preventDefault(), addSkill())}
                className="transition-all duration-200 focus:scale-[1.01] focus:shadow-md pr-10"
              />
            </div>
            <Button
              type="button"
              onClick={addSkill}
              size="default"
              className="bg-gradient-to-r from-emerald-500 to-teal-600 hover:from-emerald-600 hover:to-teal-700 text-white shadow-md hover:shadow-lg transition-all duration-200 hover:scale-105"
            >
              <Plus className="h-4 w-4 mr-1" />
              Add
            </Button>
          </div>

          <div className="flex flex-wrap gap-2 min-h-[60px] p-3 rounded-lg bg-muted/30 border border-muted">
            {skills.length === 0 ? (
              <p className="text-sm text-muted-foreground italic">No skills added yet. Add your first skill above!</p>
            ) : (
              skills.map((skill, index) => (
                <Badge
                  key={skill}
                  variant="secondary"
                  className="group px-3 py-1.5 text-sm cursor-pointer bg-gradient-to-r from-emerald-500/10 to-teal-500/10 hover:from-emerald-500 hover:to-teal-600 border-emerald-200 dark:border-emerald-800 hover:border-transparent hover:text-white transition-all duration-200 hover:scale-105 hover:shadow-md animate-in fade-in slide-in-from-bottom-2"
                  style={{ animationDelay: `${index * 50}ms` }}
                  onClick={() => removeSkill(skill)}
                >
                  <span className="font-medium">{skill}</span>
                  <X className="ml-1.5 h-3.5 w-3.5 opacity-60 group-hover:opacity-100 transition-opacity" />
                </Badge>
              ))
            )}
          </div>
        </CardContent>
      </Card>

      {/* Target Companies Card */}
      <Card className="profile-card border-0 bg-gradient-to-br from-orange-50/50 via-white to-pink-50/50 dark:from-orange-950/20 dark:via-background dark:to-pink-950/20 shadow-lg shadow-orange-500/5 dark:shadow-orange-500/10 backdrop-blur-sm transition-all duration-300 hover:shadow-xl hover:shadow-orange-500/10 dark:hover:shadow-orange-500/20">
        <CardHeader className="space-y-1 pb-4">
          <div className="flex items-center gap-2">
            <div className="p-2 rounded-lg bg-gradient-to-br from-orange-500 to-pink-600 text-white">
              <Building2 className="h-5 w-5" />
            </div>
            <CardTitle className="text-2xl bg-gradient-to-r from-orange-600 to-pink-600 dark:from-orange-400 dark:to-pink-400 bg-clip-text text-transparent">
              Target Companies
            </CardTitle>
          </div>
          <CardDescription className="text-sm">
            Companies you&apos;re preparing to interview with
          </CardDescription>
        </CardHeader>
        <CardContent className="space-y-4">
          <div className="flex gap-2">
            <div className="relative flex-1">
              <Input
                value={newCompany}
                onChange={(e) => setNewCompany(e.target.value)}
                placeholder="Add a company (e.g., Google, Amazon, Meta)"
                onKeyDown={(e) => e.key === "Enter" && (e.preventDefault(), addCompany())}
                className="transition-all duration-200 focus:scale-[1.01] focus:shadow-md"
              />
            </div>
            <Button
              type="button"
              onClick={addCompany}
              size="default"
              className="bg-gradient-to-r from-orange-500 to-pink-600 hover:from-orange-600 hover:to-pink-700 text-white shadow-md hover:shadow-lg transition-all duration-200 hover:scale-105"
            >
              <Plus className="h-4 w-4 mr-1" />
              Add
            </Button>
          </div>

          <div className="flex flex-wrap gap-2 min-h-[60px] p-3 rounded-lg bg-muted/30 border border-muted">
            {targetCompanies.length === 0 ? (
              <p className="text-sm text-muted-foreground italic">No companies added yet. Add your target companies above!</p>
            ) : (
              targetCompanies.map((company, index) => (
                <Badge
                  key={company}
                  variant="outline"
                  className="group px-3 py-1.5 text-sm cursor-pointer bg-gradient-to-r from-orange-500/10 to-pink-500/10 hover:from-orange-500 hover:to-pink-600 border-orange-200 dark:border-orange-800 hover:border-transparent hover:text-white transition-all duration-200 hover:scale-105 hover:shadow-md animate-in fade-in slide-in-from-bottom-2"
                  style={{ animationDelay: `${index * 50}ms` }}
                  onClick={() => removeCompany(company)}
                >
                  <Building2 className="mr-1.5 h-3.5 w-3.5 opacity-60 group-hover:opacity-100 transition-opacity" />
                  <span className="font-medium">{company}</span>
                  <X className="ml-1.5 h-3.5 w-3.5 opacity-60 group-hover:opacity-100 transition-opacity" />
                </Badge>
              ))
            )}
          </div>
        </CardContent>
      </Card>

      {/* Save Button */}
      <Button
        type="submit"
        className="w-full h-12 text-base font-semibold bg-gradient-to-r from-blue-600 via-purple-600 to-pink-600 hover:from-blue-700 hover:via-purple-700 hover:to-pink-700 text-white shadow-lg hover:shadow-xl transition-all duration-300 hover:scale-[1.02] disabled:opacity-50 disabled:cursor-not-allowed disabled:hover:scale-100"
        disabled={isLoading}
      >
        {isLoading ? (
          <>
            <div className="mr-2 h-5 w-5 animate-spin rounded-full border-2 border-white border-t-transparent" />
            Saving Changes...
          </>
        ) : (
          <>
            <Save className="mr-2 h-5 w-5" />
            Save Changes
          </>
        )}
      </Button>

      <style jsx>{`
        @keyframes gradient-shift {
          0%, 100% {
            background-position: 0% 50%;
          }
          50% {
            background-position: 100% 50%;
          }
        }
        
        .profile-card {
          position: relative;
          overflow: hidden;
        }
        
        .profile-card::before {
          content: '';
          position: absolute;
          top: 0;
          left: 0;
          right: 0;
          height: 2px;
          background: linear-gradient(90deg, transparent, currentColor, transparent);
          opacity: 0;
          transition: opacity 0.3s;
        }
        
        .profile-card:hover::before {
          opacity: 0.5;
        }
      `}</style>
    </form>
  );
}
